import Image from "next/image";

export default function Footer() {
  return (
    <footer className="bg-[var(--color-line-blue-100)] text-[var(--color-text-primary)] py-12 md:py-2 ">
      <div className="max-w-6xl mx-auto px-6">
        {/* ================= TOP GRID ================= */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-10 md:gap-8 mt-6">
          {/* ================= LEFT: Logo + Info + Social ================= */}
          <div className="text-center md:text-left">
            <div className="flex justify-center md:justify-start items-center mb-4">
              <Image
                src="/images/worklylogo.png"
                alt="Workly Logo"
                width={130}
                height={27}
                className="mr-2"
              />
            </div>

            <p className="text-[12px] leading-[22px] text-[var(--color-text-secondary)] opacity-90 max-w-sm mx-auto md:mx-0 mb-6 font-inter">
              The purpose of a FAQ is generally to provide information on frequent
              questions or concerns.
            </p>

            {/* ✅ Social Icons */}
            <div className="flex justify-center md:justify-start gap-3">
              {[
                { name: "twitter", link: "/images/icons/twitter.svg" },
                { name: "facebook", link: "/images/icons/facebook.svg" },
                { name: "instagram", link: "/images/icons/instagram.svg" },
                { name: "github", link: "/images/icons/github.svg" },
              ].map((icon) => (
                <a
                  key={icon.name}
                  href="#"
                  className="w-9 h-9 flex items-center justify-center rounded-full border border-[var(--color-line-orange-100)] 
                             bg-[var(--color-bg-primary-50)] transition-all duration-300 
                             hover:border-[var(--color-text-primary)] hover:bg-[var(--color-bg-neutral-50)]/5"
                >
                  <Image
                    src={icon.link}
                    alt={icon.name}
                    width={16}
                    height={16}
                    className="transition-all duration-300 brightness-0 invert-0 hover:invert hover:brightness-0 
                               hover:filter hover:sepia-0 hover:invert-[0.3] hover:saturate-[8] hover:hue-rotate-[200deg]"
                    style={{ filter: "brightness(0) invert(0)" }}
                  />
                </a>
              ))}
            </div>
          </div>

          {/* ================= CENTER: Company + Help ================= */}
          <div className="grid grid-cols-2 gap-6">
            <div>
              <h4 className="font-semibold mb-3 text-sm font-inter text-[var(--color-text-primary)]">
                Company
              </h4>
              <ul className="space-y-2 text-xs text-[var(--color-text-secondary)]">
                {["All Service", "FAQs", "About us"].map((item) => (
                  <li key={item}>
                    <a
                      href="#"
                      className="hover:text-[var(--color-primary)] transition-colors duration-200"
                    >
                      {item}
                    </a>
                  </li>
                ))}
              </ul>
            </div>

            <div>
              <h4 className="font-semibold mb-3 text-sm font-inter text-[var(--color-text-primary)]">
                Help
              </h4>
              <ul className="space-y-2 text-xs text-[var(--color-text-secondary)]">
                {[
                  "Customer Support",
                  "Terms & Conditions",
                  "Privacy Policy",
                ].map((item) => (
                  <li key={item}>
                    <a
                      href="#"
                      className="hover:text-[var(--color-primary)] transition-colors duration-200"
                    >
                      {item}
                    </a>
                  </li>
                ))}
              </ul>
            </div>
          </div>

          {/* ================= RIGHT: Newsletter ================= */}
          <div>
            <h4 className="font-semibold mb-3 text-sm font-inter text-[var(--color-text-primary)]">
              Subscribe to Newsletter
            </h4>

            <div className="relative flex items-center max-w-3xs rounded-lg overflow-hidden bg-[var(--color-btn-teritary-100)] border border-[var(--color-btn-teritary-200)] h-[38px]">
              <input
                type="email"
                placeholder="Enter email address"
                className="flex-1 h-full px-4 text-xs outline-none bg-transparent text-[var(--color-text-secondary)] placeholder-[var(--color-text-muted)]"
              />
              <button
                type="submit"
                className="absolute right-[4px] bg-[var(--color-btn-primary-500)] text-[var(--color-secondary)] text-[11px] font-semibold px-4 h-[30px]
                           transition-all duration-200 hover:bg-[var(--color-btn-primary-600)] cursor-pointer
                           rounded-lg flex items-center justify-center"
              >
                Join now
              </button>
            </div>
          </div>
        </div>

        {/* ================= BOTTOM SECTION ================= */}
        <div className="border-t border-[var(--color-btn-teritary-300)] mt-10 mb-6"></div>
        <p className="text-center text-xs text-[var(--color-text-secondary)] mb-2">
          © {new Date().getFullYear()} All Rights Reserved by{" "}
          <span className="font-semibold text-[var(--color-text-primary)]">
            Workly
          </span>
        </p>
      </div>
    </footer>
  );
}
